.countdown {
  margin: 20px;
  font-size: 1.5rem;
  color:var(--white);
  background-color: var(--skin-beige);
  text-shadow:  0 0 10px var(--white), 0 0 32px rgb(185, 130, 61);
  padding: 5px;
  border: 1px solid var(--skin-beige);
  border-radius: 10px;
  font-weight: 800;
}
.countdown h2 {
  font-size: 2.8rem;
}
.countdown-clock p {
  font-size: 1.2rem;
  display: inline-flex;
  flex-wrap: wrap;
  margin: 10px;
  
}